<template>
  <div style="width:800px;height: auto">
    <criteria-paged v-ref:page1 :model="model1" :pager='false'>
      <criteria v-ref:xiaoshoudan partial='criteria' @condition-changed='search'>
        <div novalidate class="form-inline auto" partial>
          <div class="form-group">
            <!--<span class="btn btn-default" @click="search()"></span>-->
            <!--<report-print id='shexianss' top='1cm' left='1cm' width='100%' height='100%'></report-print>-->
            <div class="col-sm-12">
              <div class="col-sm-10"></div>
              <div class="col-sm-2">
                <report-print v-ref:reportprint @click="$parent.$parent.$parent.updateNumber()" id='shexianss1' top='1cm' left='1cm' width='100%' height='100%'></report-print>
              </div>
            </div>
          </div>
        </div>
      </criteria>
      <div partial='list' id='shexianss1'>
        <table class='tablesty' style="margin: 0px auto;" border="1">
          <tr>
            <th colspan='4' style="font-weight: normal;">
              {{{ model.data.substring(26,model.data.length-8) }}}
              <!--{{model.data}}-->
            </th>
          </tr>
        </table>

      </div>
    </criteria-paged>
  </div>
</template>

<script>
  import {DataModel} from 'vue-client'

  export default {
    title: "销售单",
    props: ['billselect', 'info','billinfo'],
    data() {
      return {
        model1: new DataModel('rs/report/xiaoshoudan', {
          data: {
            billinfo: this.billinfo,
            processid: this.billselect.f_process_id,
            temp: this.info
          }
        }),
      }
    },
    methods:{
      load(){
        this.model1 = null
        this.model1=new DataModel('rs/report/xiaoshoudan', {
          data: {
            billinfo: this.billinfo,
            processid: this.billselect.f_process_id,
            temp: this.info
          }
        })
      },
      billprint(){
        this.$refs.page1.$refs.xiaoshoudan.$refs.reportprint.print()
      }
    },
    watch:{
      'model1'(){
        if(this.model1){
          this.$refs.page1.$refs.xiaoshoudan.search()
        }
      }
    }
  }
</script>

<style scoped>

</style>



